@keyframes fadeIn {
    0% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

@keyframes scaleIn {
    0% {
        transform: scale(1.3);
    }
    100% {
        transform: scale(1.1);
    }
}

.home__social {
    grid-column: 1/-1;
    position: relative;

    @include media('<mobile') {
        .wrap--social__img {
            &:nth-of-type(3) {
                display: none;
            }
        }
    }

    @include media('<tablet') {
        .wrap--social__img {
            &:nth-of-type(4),
            &:nth-of-type(5) {
                display: none;
            }
        }
    }

    @include media('>=tablet') {
        .box__border {
            border-left: 14px $border-style-solid transparent;
        }
        .box__content {
            margin: 0 $measure--small4 0 $measure--medium3;
        }
        .social__img {
            object-fit: cover;
        }
    }

    @include media('<small') {
        .box {
            @include default-margin();
            top: 50%;
            transform: translateY(-50%);
        }
    }

    @include media('>=small') {
        .box {
            margin: 0 $measure--medium5;
            &.in-view {
                top: 10rem;
            }
            &:not(.in-view) {
                opacity: 0;
                top: 17rem;
            }
        }
    }

    // Transparent left borders for triangle VI
    @include media('>=desktop') {
        .box__border {
            border-left: 16px $border-style-solid transparent;
        }
    }

    @include media('>=1080p') {
        .box__border {
            border-left: 18px $border-style-solid transparent;
        }

        // Limit image block width, similar to other content elements on page
        @include standard-box();
        width: $width--max;
    }

    @include media('>=4K') {
        .box__border {
            border-left: 36px $border-style-solid transparent;
        }
    }

    @include media('>=8K') {
        .box__border {
            border-left: 72px $border-style-solid transparent;
        }
    }

    .row {
        display: flex;
    }

    .wrap--social__img {
        overflow: hidden;

        &:nth-of-type(1) {
            .social__img {
                &.in-view {
                    animation: fadeIn $transition-duration--medium 0s,
                        scaleIn $transition-duration--medium 0s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(2) {
            .social__img {
                &.in-view {
                    animation: fadeIn $transition-duration--medium 0.1s,
                        scaleIn $transition-duration--medium 0.1s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(3) {
            .social__img {
                &.in-view {
                    animation: fadeIn $transition-duration--medium 0.2s,
                        scaleIn $transition-duration--medium 0.2s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(4) {
            .social__img {
                &.in-view {
                    animation: fadeIn $transition-duration--medium 0.3s,
                        scaleIn $transition-duration--medium 0.3s;
                    animation-fill-mode: forwards;
                }
            }
        }

        &:nth-of-type(5) {
            .social__img {
                &.in-view {
                    animation: fadeIn $transition-duration--medium 0.4s,
                        scaleIn $transition-duration--medium 0.4s;
                    animation-fill-mode: forwards;
                }
            }
        }
    }

    .social__img {
        opacity: 0;
        filter: brightness(0.5) saturate(100%);
    }

    .box {
        position: absolute;
        background: $color-purewhite;
        padding: $measure--small4 $measure--basic $measure--medium3;
        transition: top 0.3s;
        z-index: z-index('above');
    }

    .box__pre-strap {
        text-transform: uppercase;
        display: flex;
        font-weight: $font-weight-bold;
        span {
            display: block;
            &.text {
                color: $color-brand-primary;
                @include font-family('heading');
                @include heading-font-features();
            }
            &.line {
                margin: auto 0 auto $measure--basic;
                flex: 1;
            }
        }
    }

    .line {
        flex: 1;
        border-top: $border-width-thicker + ($border-width-thicker / 2)
            $border-style-solid $color-brand-primary;
        margin: $measure--threequarters 0 $measure--small1;
    }

    .box__content__body {
        border-bottom: $border-width-hairline $border-style-solid solid
            $color-gray6;
        margin-bottom: $measure--basic;
    }

    .box__post-strap {
        .icon__wrapper {
            background-color: $color-brand-primary;
            border-radius: $measure--basic;
            color: $color-purewhite;
            display: inline-flex;
            height: $measure--small4;
            width: $measure--small4;
            margin-right: $measure--half;

            .icon {
                margin: auto;
                &.fa-instagram {
                    &:before {
                        @include gammatype();
                    }
                }
            }

            .line {
                margin-bottom: $measure--small1;
            }
        }

        a.anchor-discreet {
            color: $color-brand-primary;
            border-bottom-color: $color-brand-primary;
            font-weight: $font-weight-medium;
            bottom: $measure--quarter / 2;

            &:hover {
                @include media('hover') {
                    &::before {
                        opacity: 0;
                        transition: none;
                    }
                }
            }

            &:focus {
                outline-style: solid;
            }
        }
    }

    // Create double grey triangle on left border
    .box__border {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -1px; // To align with box's left border
        right: 0;
        background: transparent;
        width: 0;
        border-image: url(https://www.city.ac.uk/__data/assets/file/0015/710241/community.svg)
            65 fill repeat;
        filter: invert(67%) sepia(1%) saturate(0%) hue-rotate(151deg)
            brightness(98%) contrast(85%);
    }

    // Position second border to avoid overlap
    .box__border--two {
        left: $measure--threequarters;
    }
}
